$window: 800px;
$white: #eee;
$grey: #b1b1b1;
$beight: #e9b047;
$primary-color: #925f00;

@mixin bg-white {
  background-color: $white;
}

@mixin _shadow_($alpha) {
  -webkit-box-shadow: 0px 0px 11px -2px rgba(0, 0, 0, $alpha);
  -moz-box-shadow: 0px 0px 11px -2px rgba(0, 0, 0, $alpha);
  box-shadow: 0px 0px 11px -2px rgba(0, 0, 0, $alpha);
}

// Global style
.insert-product-page {
  width: 100%;
  .insert-product-form {
    width: 100%;
    .insert-product-form-head {
      width: 100%;
      color: $primary-color;
    }
    .insert-product-form-row {
      width: 100%;
      label {
        display: block;
        width: 100%;
        color: $primary-color;
      }
      input {
        &:not(input[type="file"]) {
          display: block;
          width: 100%;
          outline: none;
          border: 1px solid #b1b1b1;
        }
        &:is(input[type="file"]) {
          position: absolute;
          z-index: -1;
          opacity: 0;
        }
      }
      .img-input-btn {
        width: 100%;
        text-align: center;
        color: $primary-color;
        background-color: $beight;
        border: 1px solid $primary-color;
        p {
          font-weight: bold;
        }
      }
      .img-input-btn:active {
        opacity: 0.9;
      }
      textarea {
        display: block;
        width: 100%;
        outline: none;
        border: 1px solid #b1b1b1;
        resize: none;
      }
      .img-preview {
        width: 100%;
        visibility: hidden;
        opacity: 0;
        transition: 0.3s all ease;
        height: 0px;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        overflow: hidden;
        p {
          display: flex;
          align-items: center;
          justify-content: center;
          height: 100%;
          width: 100%;
          font-weight: bold;
          color: $primary-color;
          background-color: rgba($color: $beight, $alpha: 0.8);
        }
      }
      .img-preview-active {
        visibility: visible;
        opacity: 1;
      }
    }
    .insert-product-form-btns {
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      button {
        display: block;
        width: 50%;
        outline: none;
        border: 1px solid $primary-color;
        color: $primary-color;
        font-weight: bold;
        background: $beight;
      }
      .btn-spacer {
        display: block;
      }
    }
  }
}

// PC style
@media screen and(min-width: $window) {
  .insert-product-page {
    .insert-product-form {
      .insert-product-form-head {
      }
      .insert-product-form-row {
      }
    }
  }
}

// Mobile style
@media screen and(max-width: $window) {
  .insert-product-page {
    .insert-product-form {
      padding: 20px;
      .insert-product-form-head {
        font-size: 22px;
        text-align: center;
        margin-bottom: 5px;
      }
      .insert-product-form-row {
        &:not(.insert-product-form-row:nth-last-child(1)) {
          margin-bottom: 10px;
        }
        label {
          font-size: 15px;
          padding: 0px 15px 5px 15px;
        }
        input {
          &:not(input[type="file"]) {
            height: 40px;
            min-height: 40px;
            border-radius: 20px;
            padding: 0px 15px 0px 15px;
            font-size: 15px;
          }
        }
        .img-input-btn {
          height: 40px;
          min-height: 40px;
          line-height: 38px;
          border-radius: 20px;
          padding: 0px 15px 0px 15px;
          p {
            font-size: 15px;
          }
        }
        textarea {
          height: 100px;
          min-height: 100px;
          border-radius: 20px;
          padding: 15px;
          font-size: 15px;
        }
        .img-preview {
          border-radius: 20px;
          p {
            font-size: 15px;
          }
        }
      }
      .insert-product-form-btns {
        button {
          height: 40px;
          min-height: 40px;
          font-size: 15px;
          &:is(button:nth-child(1)) {
            border-radius: 20px 0px 0px 20px;
          }
          &:is(button:nth-last-child(1)) {
            border-radius: 0px 20px 20px 0px;
          }
        }
        .btn-spacer {
          width: 7px;
          min-width: 7px;
        }
      }
    }
  }
}
